
.app {
  position: relative;
  display: grid;
  grid-template-rows: 12.5% 75% 10%;
  height: 100vh;
  max-width: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

.intro {
  margin: 0px;
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  align-self: center;
  justify-self: center;
  color: black;
  text-shadow: 1px 1px 1px black;
  opacity: 0;
  -webkit-animation: intro 4s;
          animation: intro 4s;
}

.welcome-container {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: -webkit-box;
  display: flex;
  margin: 0px;
  -webkit-box-pack: center;
          justify-content: center;
  background: rgba(0,0,0,0.55);
  -webkit-animation: welcome 0.5s;
          animation: welcome 0.5s;
}

.welcome{  
  font-size: 2.5rem;
  color: whitesmoke;
  text-shadow: 3px 3px 3px black;  
  align-self: center;
}

.canvas {
  grid-column: 1 / 2;
  grid-row: 1 / 4;
  background-color: lightgray;
  z-index: -1;
  -webkit-animation: welcome 0.5s;
          animation: welcome 0.5s; 
  width: 100%;
  height: 100%;
}

.item-container, .about-container, .resume-container{
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  -webkit-box-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.about-container, .resume-container{
  -webkit-animation: welcome 0.5s;
          animation: welcome 0.5s;
}

.resume-container > img {
  margin:2rem;
  border: 5px solid #020122;
  border-radius: 10px;
  box-shadow: 0px 0px 40px 10px rgba(0,0,0,0.5);
  max-width: 90%;
}

.about {
  background: rgba(255,255,255,.9);
  border-radius: 10px;
  padding: 40px;
  border: 3px solid black;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  box-shadow: 2px 2px 10px 3px rgba(0,0,0,0.5);
  margin: 20px;  
}

.about p {
  font-size: 1.25rem;
  -webkit-box-flex: 1;
          flex: 1;
}

.about > * {
  text-align: center;
}

.item {
  border-width: 2px;
  padding: 40px;
  display: -webkit-box;
  display: flex;
  align-self: stretch;
}

.item a {
  -webkit-box-flex: 1,;
          flex: 1, 1, 150px;
  display: -webkit-box;
  display: flex;
  align-self: center;
}

.item a img {
  -webkit-box-flex: 1;
          flex: 1;
  box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.5);
  align-self: center;
  border-radius: 10px;
  width: 250px;
  height: 250px;
}

.item a img:hover {
  box-shadow: 0px 0px 40px 1px rgba(0,0,0,0.5);
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.itema {
  -webkit-animation: items .5s;
          animation: items .5s
}

.itemb {
  -webkit-animation: items .75s;
          animation: items .75s
}
.itemc {
  -webkit-animation: items 1s;
          animation: items 1s
}
.itemd {
  -webkit-animation: items 1.25s;
          animation: items 1.25s
}

.navBar {
  margin: 0px;
  padding: 0px;
  grid-column: 1/2;
  grid-row: 3/4;
  list-style: none;
  background: rgba(0,0,0,0.6);
  display: -webkit-box;
  display: flex;
}

.navLinks {
  -webkit-box-flex: 3;
          flex: 3;
  list-style: none;
  display: -webkit-box;
  display: flex;
  font-size: 1rem;
  color: whitesmoke;
  text-shadow: 3px 3px 3px black;  
}

.navLinks > li {
  -webkit-box-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: flex;
}

.navLinks > li > span {
  -webkit-box-flex: 1;
          flex: 1;
  align-self: center;
  text-align: center;
}

.mediaLinks{
  display: -webkit-box;
  display: flex;
  -webkit-box-flex: 1;
          flex: 1;
  justify-items: center;
}

.mediaLinks > a {
  -webkit-box-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: flex;
  justify-items: center;
  -webkit-box-pack: center;
          justify-content: center;
}

.mediaLinks > a:hover, .navLinks > li:hover {
  background: rgba(255,255,255,0.5);
}

.navImg {
  height: 20px;
  width: 20px;
  align-self: center;
  -webkit-filter: invert();
          filter: invert();
  opacity: 0.7;
}

@media all and (max-width: 789px){
  .navBar{
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    flex-wrap: wrap;
  }

  .navLinks {
    -webkit-box-flex: 1;
            flex: 1 1 65%;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
            flex-direction: row;
  }

  .navLinks > li {
    -webkit-box-flex: 1;
            flex: 1;
  }
  
  .mediaLinks {
    -webkit-box-flex: 1;
            flex: 1 1 35%;
    width: 100%;
    -webkit-box-ordinal-group: 2;
            order: 1
  }

  .mediaLinks > a {
    -webkit-box-flex: 1;
            flex: 1
  }
  
  .navImg {
    height: 12.5px;
    width: 12.5px;
  }
}

@-webkit-keyframes welcome {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes welcome {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes items {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0
  }
  100% {
    opacity: 1
  }
}

@keyframes items {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0
  }
  100% {
    opacity: 1
  }
}

@-webkit-keyframes intro {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes intro {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}